<template>
  <div id="loginBox">
    <div id="login" v-if="showType">
      <div class="header-nav flex-row jusB">
        <div class="backIcon iconSize flex-row" @click="closeFn">
          <img src="../../../assets/img/black-left.png" alt="返回">
        </div>
      </div>
      <div class="logo posC">
        <div class="iconSize">
          <img src="../../../assets/img/logo.png" alt="logo">
        </div>
      </div>
      <div class="logoText posC">看资讯影视，赚车子赚房子</div>
      <div class="loginButton posC phone flex-row" :class="{isBrower: isBrower}"  @click="phoneLogin">
        <div class="leftIcon flex-row">
          <div class="iconSize">
            <img src="../../../assets/img/personal/login_phone.png" alt="手机登录图标">
          </div>
        </div>
        <div class="text">手机号登录&nbsp;&nbsp;</div>
      </div>
      <div class="loginButton posC flex-row wechat" @click="wechatLogin">
        <div class="leftIcon flex-row">
          <div class="iconSize">
            <img src="../../../assets/img/personal/login_wechat.png" alt="微信登录图标">
          </div>
        </div>
        <div class="text">微信一键登录</div>
      </div>
    </div>
    <!-- 手机号登录 -->
    <div class="maskBox active" v-if="showPhone">
      <div class="phoneLogin">
        <div class="close iconSize" @click="closePhone">
          <!-- <img src="../../../assets/img/task/close_gray.png" alt="关闭"> -->
        </div>
        <div class="title">登录/注册</div>
        <div class="inputBox flex-row">
          <input type="number" v-model="mobile" pattern="\d*" placeholder="请输入11位手机号码">
        </div>
        <div class="inputBox flex-row" v-if="showCaptureCode">
          <input class="flex1" type='text' v-model="captcha_code" maxlength='4' placeholder='请输入图形验证码'>
          <div class="codeButton borderNone flex-row">
            <div class="imageCode iconSize">
              <img :src="origin + timestamp" @click="timestamp=new Date().getTime()" alt="图形验证码">
            </div>
          </div>
        </div>
        <div class="inputBox flex-row">
          <input class="flex1" maxlength='4' v-model="code" placeholder="请输入验证码" pattern="\d*" type="number">
          <div class="codeButton flex-row" :class='{active: mobile.length === 11 && buttonText === "获取验证码"}'  @click="getCode">{{buttonText}}</div>
        </div>
        <div class="loginButton flex-row" @click="login">登录/注册</div>
        <div class="tip flex-row">
          <div class="text">注册即代表同意</div>
          <div class="agreeText" @click.stop="$store.commit('common_protocol_bol', true)">《掘金宝用户协议》</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
export default {
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    this.origin = this.$config.currentApi + '/v1/passport/captcha?timestamp='
    return {
      mobile: '', // 手机号
      captcha_code: '', // 验证码
      code: '', // 手机验证码
      showCaptureCode: false, // 显示图形验证码
      buttonText: '获取验证码',
      timestamp: new Date().getTime(),
      isBrower: false,
      protocolBol: false,
      couldSendMsg: true,
      loginBtn: true,
      showType: true,
      showPhone: false
    }
  },
  methods: {
    ...mapMutations([
      'common_login_bol',
      'common_login_box',
      'common_index_column',
      'common_set_userInfo',
      'common_protocol_bol',
      'common_get_channel',
      'common_get_recondChannel'
    ]),
    // 关闭
    closeFn () {
      this.$emit('closeFn')
    },
    closePhone () {
      this.showPhone = false
      if (this.$config.source_style <= 6 && !this.$utils.isWx()) {
        this.closeFn()
      }
    },
    setInput () {
      if (this.mobile) {
        this.mobile = this.mobile.replace(/[^0-9]/g, '')
        if (this.mobile.length > 11) {
          this.mobile = this.mobile.substr(0, 11)
        }
      }
      if (this.code) {
        this.code = this.code.replace(/[^0-9]/g, '')
        if (this.code.length > 11) {
          this.code = this.code.substr(0, 4)
        }
      }
    },
    // 显示手机号登录弹框
    phoneLogin () {
      this.showPhone = true
    },
    // 微信选择微信登录还是app登录
    wechatLogin () {
      if (this.$config.source_style < 6) {
        this.$utils.wxLogin()
      } else {
        this.appLogin()
      }
    },
    // 获取验证码
    getCode () {
      if (!this.couldSendMsg) return
      if (this.buttonText !== '获取验证码') {
        return
      }
      // 两s后恢复可点击状态
      setTimeout(() => {
        this.couldSendMsg = true
      }, 2000)
      if (!this.$utils.isReg('PHONE_NUMBER', this.mobile)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.showCaptureCode && this.captcha_code === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        let data = {}
        data.mobile = this.mobile
        if (this.showCaptureCode) {
          data.captcha_code = this.captcha_code
        }
        this.couldSendMsg = false
        this.$api.get('SMSCODE', data, (res) => {
          this.$toast({
            message: res.msg,
            duration: 1500
          })
          this.numTxt = 60
          this.buttonText = `重新发送(${this.numTxt}s)`
          this.tiemr = setInterval(() => {
            this.numTxt--
            this.buttonText = `重新发送(${this.numTxt}s)`
            if (this.numTxt <= 0) {
              clearInterval(this.tiemr)
              this.couldSendMsg = true
              this.buttonText = '获取验证码'
            }
          }, 1000)
        }, (res) => {
          if (res.msg === '请输入图形验证码') {
            this.showCaptureCode = true
            this.couldSendMsg = true
          }
          if (res.msg === '请输入图形验证码' || res.msg === '图形验证码不正确' || res.msg === '手机验证码不正确') {
            this.timestamp = new Date().getTime()
            this.couldSendMsg = true
          }
        })
      }
    },

    // 手机号登录
    login () {
      if (!this.loginBtn) {
        return
      }
      this.loginBtn = false
      setTimeout(() => {    // 过5秒可以重新点
        this.loginBtn = true
      })
      if (!this.$utils.isReg('PHONE_NUMBER', this.mobile)) {
        this.$toast({
          message: '请输入正确手机号',
          position: 'middle',
          duration: 1500
        })
      } else if (this.captcha_codeBol && this.captcha_code === '') {
        this.$toast({
          message: '请输入图形验证码',
          position: 'middle',
          duration: 1500
        })
      } else if (this.code === '') {
        this.$toast({
          message: '请输入短信验证码',
          position: 'middle',
          duration: 1500
        })
      } else {
        let data = this.$utils.registerInfo()
        data.account = this.mobile
        data.code = this.code
        if (this.$utils.cache('inviteCodeUser')) {              // 有分享码就传分享码
          data.invitation = this.$utils.cache('inviteCodeUser')
        }
        if (this.showCaptureCode) {
          data.captcha_code = this.captcha_cod
        }
        // app登录添加推送设备id
        if (this.commonState.pushInfo.clientid && this.commonState.pushInfo.clientid !== null && this.commonState.pushInfo.clientid !== 'null') {
          data.getui_cid = this.commonState.pushInfo.clientid
        }
        this.$api.post('LOGIN', data, (res) => {
          this.$utils.cache('uid', res.data.uid)
          this.$utils.cache('user_token', res.data.user_token)
          this.$utils.cache('nickname', res.data.nickname)
          this.$utils.cache('first_login', res.data.first_login)
          this.$utils.cache('inviteCode', res.data.invitation)
          this.$utils.cache('user_icon', res.data.avatar)
          this.$utils.cache('user_city', res.data.city)
          this.$utils.cache('userInfo', JSON.stringify(res.data))
          this.setShowGift()
          clearInterval(this.tiemr)
          this.numTxt = 0
          this.buttonText = `获取验证码`
          this.common_index_column(true)
          this.common_set_userInfo(res.data)
          this.common_login_bol(true)
          this.getChannel()
         
          this.$utils.cache('inviteCodeUser', null)    // 用完分享码后删除
          this.$emit('closeFn') // 关闭登录框
          this.mobile = '' // 输入框边控
          this.code = ''
          this.captcha_code = ''
          this.$toast({
            message: res.msg,
            position: 'middle',
            duration: 3000
          })
          this.loginBtn = true
        }, (res) => {
          this.loginBtn = true
          if (res.msg === '请输入图形验证码') {
            this.showCaptureCode = true
          }
          if (res.msg === '请输入图形验证码' || res.msg === '图形验证码不正确' || res.msg === '手机验证码不正确') {
            this.timestamp = new Date().getTime()
          }
        })
      }
    },
    // 是否显示大礼包
    setShowGift () {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.timer = setTimeout(() => {
        // 新用户，未领取大礼包，今天是否提醒过
        let showBigGifts = this.$utils.cache('showBigGifts') ? JSON.parse(this.$utils.cache('showBigGifts')) : null
        // 如果缓存存在并且缓存时间戳小于当前时间戳可弹起
        if (showBigGifts && showBigGifts.timeStamp < this.$utils.getTimeStamp()) {
          this.$utils.cache('showBigGifts', null)
          showBigGifts = null
        }
        if (this.commonState.userInfo.is_new === 1 && this.commonState.userInfo.achieved_gift_bag === 0 && !showBigGifts && this.$route.path === '/') {
          this.$store.commit('setShowShare', true)
          this.$store.commit('set_shareType', 9)
          this.$utils.cache('showBigGifts', JSON.stringify({
            timeStamp: this.$utils.getTodayStamp()
          }))
        }
      }, 1000)
    },
    // 获取频道
    getChannel () {
      this.$api.get('MY_CHANNEL', {}, (res) => {
        this.common_get_channel(res.data, 'my_channel')
      })
      this.$api.get('RECOND_CHANNEL', {}, (res) => {
        this.common_get_recondChannel(res.data, 'recond_channel')
      })
    },
    // app登录
    appLogin () {
      var that = this
      if (this.$config.source_style === 6 || this.$config.source_style === 7) {
        if (window.plus) {
          setLogin()
        } else {
          document.addEventListener('plusready', function () {
            setLogin()
          })
        }
      }
      function setLogin () {
        window.plus.oauth.getServices(function (service) {
          var authWeiXin = {}
          for (let i = 0; i < service.length; i++) {
            if (service[i].id === 'weixin') {
              authWeiXin = service[i]
            }
          }
          if (window.plus.runtime.isApplicationExist({ pname: 'com.tencent.mm', action: 'weixin://' })) {
            authWeiXin.authorize(function (e) {
              let obj = that.$utils.registerInfo()
              let data = {
                type: 2, // app登录
                code: e.code,
                os: obj.os,
                channel: obj.channel,
                useragent: obj.useragent
              }
              // app登录添加推送设备id
              if (that.commonState.pushInfo && that.commonState.pushInfo.clientid && that.commonState.pushInfo.clientid !== null && that.commonState.pushInfo.clientid !== 'null') {
                data.getui_cid = that.commonState.pushInfo.clientid
              }
              console.log(JSON.stringify(data))
              that.$api.post('WECHAT_LOGIN', data, function (res) {
                that.$utils.cache('uid', res.data.uid)
                that.$utils.cache('user_token', res.data.user_token)
                that.$utils.cache('nickname', res.data.nickname)
                that.$utils.cache('first_login', res.data.first_login)
                that.$utils.cache('user_icon', res.data.avatar)
                that.$utils.cache('inviteCode', res.data.invitation)
                that.$utils.cache('userInfo', JSON.stringify(res.data))
                that.common_index_column(true)
                that.common_login_bol(true)
                that.common_set_userInfo(res.data)
                that.setShowGift()
                // 登录成功获取频道
                that.getChannel()
                that.$toast({
                  message: '登录成功',
                  position: 'middle',
                  duration: 1500,
                  icon: 'icon icon-success'
                })
                that.$emit('closeFn') // 关闭登录框
                that.back()
              }, function (e) {
                if (e.code !== 0) {
                  that.$toast({
                    message: '登录失败',
                    position: 'middle',
                    duration: 1500
                  })
                  that.$emit('closeFn') // 关闭登录框
                }
              })
            }, function (e) {
              that.closeFn('closeFn')
            }, {scope: 'snsapi_userinfo', state: 'authorize test'})
          } else {
            that.$toast('请安装微信')
            that.closeFn()
          }
        })
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      this.showCaptureCode = false
      this.captcha_code = ''
      this.showType = true
      if (this.$config.source_style < 6 && !this.$utils.isWx()) {
        this.showPhone = true
        this.isBrower = true
        this.showType = false
      }
    }
  },
  beforeUpdate () {
    this.setInput()
  },
  mounted () {
   
  }
}
</script>
<style lang="scss" scoped>
#loginBox {
  box-sizing: border-box;
  position: absolute;
  position: fixed;
  width: 100vw;
  height: 100vh;
  transform: translate3d(0, 0, 0);
  #login {
    height: 100%;
    box-sizing: border-box;
    position: relative;
    background: #fff;
    .header-nav {
      height: 88px;
      box-sizing: border-box;
      border-bottom: 1px solid #f5f5f5;
      .backIcon {
        width: 20px;
        height: 35px;
        padding: 20px 40px;
      }
    }
    .logo{
      top: 270px;
      display: flex;
      justify-content: center;
      .iconSize {
        width: 180px;
        height: 180px;
        box-shadow: 0 2px 4px 0 #FF7600;
        border-radius: 30px;
        overflow: hidden;
      }
    }
    .logoText {
      top: 490px;
      white-space: nowrap;
      color: #666;
      font-size: 28px;
    }
    .loginButton {
      width: 533px;
      height: 105px;
      font-size: 36px;
      color: #FFFFFF;
      overflow: hidden;
      box-sizing: border-box;
      .leftIcon {
        height: 105px;
        width: 105px;
        .iconSize {
          width: 60px;
          height: 48px;
        }
      }
    }
    .wechat{
      // background-image: linear-gradient(55deg, #FF8B29 0%, #FFA900 100%);
      background: #24b637;
      // box-shadow: 0 2px 4px 0 #FF7000;
      border-radius: 30px;
      top: 622px;
    }
    .phone {
      background: #FFFFFF;
      box-shadow: -2px 2px 4px 0 #E0E0E0, 2px 2px 4px 0 #E0E0E0;
      border-radius: 30px;
      top: 793px;
      color: #999;
      .leftIcon {
        .iconSize {
          width: 32px;
          height: 48px;
        }
      }
      &.isBrower {
        top: 622px;
      }
    }
  }
  .maskBox {
    background: none;
  }
  .maskBox.active {
    background: rgba($color: #000000, $alpha: .5)
  }
  // 手机登录
  .phoneLogin {
    width: 596px;
    text-align: center;
    background: #fff;
    font-size: 36px;
    .close {
      position: absolute;
      right: 10px;
      top: 10px;
      width: 60px;
      height: 60px;
      background: url('../../../assets/img/task/close_gray.png') no-repeat center;
      background-size: 26px 26px;
      min-width: 0;
      img {
        height: 100%;
      }
    }
    .title {
      padding: 40px 0;
      color: #333;
    }
    .inputBox {
      border: 1px solid #979797;
      background: #fff;
      border-radius: 42px;
      height: 83px;
      width: 428px;
      margin: 0 auto;
      overflow: hidden;
      margin-bottom: 20px;
      font-size: 28px;
      color: #666;
      input {
        font-size: 28px;
        color: #666;
        border: none;
        outline: none;
        flex: 1;
        padding: 0 20px;
        min-width: 0;
        display: block;
        height: 60px;
      }
      .codeButton {
        width: 200px;
        height: 44px;
        border-left: 1px solid #979797;
        .imageCode {
          width: 200px;
          height: 62px;
        }
        &.active{
          color: #ff3000;
        }
      }
      
    }
    .loginButton {
      background-image: linear-gradient(-119deg, #FFA500 0%, #FF6C00 100%);
      border-radius: 26px;
      width: 428px;
      height: 77px;
      margin: 0 auto;
      color: #fff;
      font-size: 32px;
    }
    .tip {
      font-size: 24px;
      white-space: nowrap;
      height: 100px;
      >div{
        padding: 20px 0;
      }
      .agreeText {
        color:#FFA500
      }
    }
  }
}
</style>



